export class AppGoogleMapEnv {
    id: string;
    // appV1: AppMatterV1;
    hostDiv: HTMLElement;
    div: HTMLElement;
    version: number = 1;
    static Init(id: string = 'SandBox') {
        new AppGoogleMapEnv(id, 1);
    }
    constructor(id: string = 'SandBox', version: number = 1) {
        this.id = id;
        this.version = version;
        this.Init(id);

    }
    private Init(id: string) {

        switch (this.version) {
            case 1:
                new AppGMapV1(id);
                break;
            case 2:
                // this.appV1 = new NBlockV1(this.id);
                break;

            default:
                break;
        }



    }
}

export class Solution {
    public divHost: HTMLElement;

    constructor(id: string ) {
        this.divHost = document.getElementById(id) as HTMLDivElement;

        AppGoogleMapEnv.Init(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}